import React, {useCallback} from 'react';

import './Continent.scss';
import {useCurrentRegions, useCurrentLands} from './LandContext';
import Regions from './Regions';
import OverlappingLand from './OverlappingLand';

const valueToPercent = (
  value: number,
  min: number,
  max: number,
  margin: number,
) => (100 * (value - min + margin)) / (max - min + margin + margin);

export default function Continent() {
  const lands = useCurrentLands();
  const extents: undefined | number[] = React.useMemo(
    () =>
      lands.reduce((a, land) => {
        if (a === undefined) {
          return [land.x1, land.x2, land.y1, land.y2];
        }
        return [
          a[0] > land.x1 ? land.x1 : a[0],
          a[1] < land.x2 ? land.x2 : a[1],
          a[2] > land.y1 ? land.y1 : a[2],
          a[3] < land.y2 ? land.y2 : a[3],
        ];
      }, undefined as undefined | number[]),
    [lands],
  );
  const getX = useCallback(
    (x: number) =>
      extents ? valueToPercent(x, extents[0], extents[1], 0.5) : 0,
    [extents],
  );
  const getY = useCallback(
    (y: number) => (extents ? valueToPercent(y, extents[2], extents[3], 1) : 0),
    [extents],
  );
  const {overlappingLands, regions} = useCurrentRegions();
  return (
    <>
      <div className="continent">
        <Regions regions={regions} getX={getX} getY={getY} colourOffset={0} />
        {overlappingLands.map((land, i) => (
          <OverlappingLand
            key={`overlappingLand${i}`}
            getX={getX}
            getY={getY}
            land={land}
          />
        ))}
      </div>
    </>
  );
}
