import React, {
  useState,
  createContext,
  useCallback,
  useContext,
  useMemo,
} from 'react';

import {Land, regionsFromLands, Regions, sameLandAs} from './Land';

const defaultLands: Land[] = [
  {x1: 1.0, y1: 1.0, x2: 10.0, y2: 6.0},
  {x1: 1.5, y1: 1.5, x2: 6.0, y2: 5.0},
  {x1: 2.0, y1: 2.0, x2: 3.0, y2: 3.0},
  {x1: 2.0, y1: 3.5, x2: 3.0, y2: 4.5},
  {x1: 3.5, y1: 2.0, x2: 5.5, y2: 4.5},
  {x1: 4.0, y1: 3.5, x2: 5.0, y2: 4.0},
  {x1: 4.0, y1: 2.5, x2: 5.0, y2: 3.0},
  {x1: 7.0, y1: 3.0, x2: 9.5, y2: 5.5},
  {x1: 7.5, y1: 4.0, x2: 8.0, y2: 5.0},
  {x1: 8.5, y1: 3.5, x2: 9.0, y2: 4.5},
  {x1: 3.0, y1: 7.0, x2: 8.0, y2: 10.0},
  {x1: 5.0, y1: 7.5, x2: 7.5, y2: 9.5},
  {x1: 5.5, y1: 8.0, x2: 6.0, y2: 9.0},
  {x1: 6.5, y1: 8.0, x2: 7.0, y2: 9.0},
];

interface UpdateFunctions {
  newLand: (land: Land) => void;
  deleteLand: (index: number) => void;
  updateLand: (index: number, newLand: Land) => void;
}

interface State {
  lands: Land[];
  regions: Regions;
}

const stateFromLands = (lands: Land[]) => ({
  lands,
  regions: regionsFromLands(lands),
});

const StateContext = createContext<State | undefined>(undefined);
const UpdateContext = createContext<UpdateFunctions | undefined>(undefined);

interface EditState {
  editingMode: boolean;
  landIsHovered: (l: Land) => boolean;
  landBeingEdited: (l: Land) => boolean;
  setHoveredLand: (l: Land | undefined) => void;
  setEditingLand: (l: Land | undefined) => void;
}

const EditContext = createContext<EditState | undefined>(undefined);

export default function LandContext({children}: {children: React.ReactNode}) {
  const [currentLands, setCurrentLands] = useState(
    stateFromLands(defaultLands),
  );
  const newLand = useCallback((land: Land) => {
    setCurrentLands(oldLands => stateFromLands([...oldLands.lands, land]));
  }, []);
  const deleteLand = useCallback((index: number) => {
    setCurrentLands(oldLands => {
      const newLands = [...oldLands.lands];
      newLands.splice(index, 1);
      return stateFromLands(newLands);
    });
  }, []);
  const updateLand = useCallback((index: number, newLand: Land) => {
    setCurrentLands(oldLands =>
      stateFromLands(
        oldLands.lands.map((oldLand, i) => (i === index ? newLand : oldLand)),
      ),
    );
  }, []);
  const [hoveredLand, setHoveredLand] = useState<undefined | Land>(undefined);
  const [editingLand, setEditingLand] = useState<undefined | Land>(undefined);
  const landIsHovered = useMemo(
    () => (hoveredLand ? sameLandAs(hoveredLand) : () => false),
    [hoveredLand],
  );
  const landBeingEdited = useMemo(
    () => (editingLand ? sameLandAs(editingLand) : () => false),
    [editingLand],
  );
  return (
    <StateContext.Provider value={currentLands}>
      <UpdateContext.Provider value={{newLand, deleteLand, updateLand}}>
        <EditContext.Provider
          value={{
            landIsHovered,
            landBeingEdited,
            setHoveredLand,
            setEditingLand,
            editingMode: editingLand !== undefined,
          }}>
          {children}
        </EditContext.Provider>
      </UpdateContext.Provider>
    </StateContext.Provider>
  );
}

export function useCurrentLands() {
  const state = useContext(StateContext);
  if (state === undefined) {
    throw new Error(
      'useCurrentLands must be used in a child of <LandContext />',
    );
  }
  return state.lands;
}

export function useCurrentRegions() {
  const state = useContext(StateContext);
  if (state === undefined) {
    throw new Error(
      'useCurrentRegions must be used in a child of <LandContext />',
    );
  }
  return state.regions;
}

export function useUpdateCurrentLands() {
  const update = useContext(UpdateContext);
  if (update === undefined) {
    throw new Error(
      'useUpdateCurrentLands must be used in a child of <LandContext />',
    );
  }
  return update;
}

export function useEditLands() {
  const edit = useContext(EditContext);
  if (edit === undefined) {
    throw new Error('useEditLands must be used in a child of <LandContext />');
  }
  return {
    setEditingLand: edit.setEditingLand,
    setHoveredLand: edit.setHoveredLand,
  };
}

export function useEditingLands() {
  const edit = useContext(EditContext);
  if (edit === undefined) {
    throw new Error('useEditLands must be used in a child of <LandContext />');
  }
  return {
    editingMode: edit.editingMode,
    landIsHovered: edit.landIsHovered,
    landBeingEdited: edit.landBeingEdited,
  };
}
