export interface Land {
  x1: number;
  y1: number;
  x2: number;
  y2: number;
}

export function landContains(a: Land, b: Land): boolean {
  return a.x1 < b.x1 && a.x2 > b.x2 && a.y1 < b.y1 && a.y2 > b.y2;
}

export function landOverlaps(a: Land, b: Land): boolean {
  return (
    !landContains(a, b) &&
    !landContains(b, a) &&
    b.x1 <= a.x2 &&
    b.x2 >= a.x1 &&
    b.y1 <= a.y2 &&
    b.y2 >= a.y1
  );
}

export interface Region {
  land: Land;
  children: Region[];
}

export const numberOfLands = (regions: Region[]): number =>
  regions.reduce(
    (a, region) =>
      a +
      (region.children.length === 0 ? 1 : 1 + numberOfLands(region.children)),
    0,
  );

const overlaps = (land: Land) => (otherLand: Land) =>
  landOverlaps(otherLand, land);

const doesntOverlap = (land: Land) => (otherLand: Land) =>
  !landOverlaps(otherLand, land);

const regionContainedBy = (land: Land) => (region: Region) =>
  landContains(land, region.land);

const regionNotContainedBy = (land: Land) => (region: Region) =>
  !landContains(land, region.land);

const regionContains = (land: Land) => (region: Region) =>
  landContains(region.land, land);

export interface Regions {
  overlappingLands: Land[];
  regions: Region[];
}

interface AnalysedLands {
  nonOverlapping: Land[];
  overlappingLands: Land[];
}

function regionsFromChildLand(regions: Region[], land: Land): Region[] {
  const childRegions = regions.filter(regionContainedBy(land), []);
  if (childRegions.length > 0) {
    return [
      ...regions.filter(regionNotContainedBy(land)),
      {land, children: childRegions},
    ];
  }
  const parentRegionIndex = regions.findIndex(regionContains(land));
  if (parentRegionIndex === -1) {
    return [...regions, {land, children: []}];
  }
  return regions.map((region, i) =>
    i !== parentRegionIndex
      ? region
      : {
          land: region.land,
          children: regionsFromChildLand(region.children, land),
        },
  );
}

export function regionsFromLands(lands: Land[]): Regions {
  const {nonOverlapping, overlappingLands} = lands.reduce(
    ({nonOverlapping, overlappingLands}, land) => {
      const stillNonOverlapping = nonOverlapping.filter(doesntOverlap(land));
      if (stillNonOverlapping.length === nonOverlapping.length) {
        return {
          nonOverlapping: [...nonOverlapping, land],
          overlappingLands,
        };
      }
      const nowOverlapping = nonOverlapping.filter(overlaps(land));
      return {
        nonOverlapping: stillNonOverlapping,
        overlappingLands: [...overlappingLands, ...nowOverlapping, land],
      };
    },
    {nonOverlapping: [], overlappingLands: []} as AnalysedLands,
  );
  const regions = nonOverlapping.reduce(regionsFromChildLand, [] as Region[]);
  return {
    overlappingLands,
    regions,
  };
}

export const sameLandAs = (land: Land) => (other: Land) =>
  JSON.stringify(land) === JSON.stringify(other);
