import React from 'react';
import classnames from 'classnames';

import {Region as RegionType} from './Land';
import {greens} from './Colours';
import {useEditingLands} from './LandContext';

interface RegionProps {
  getX: (x: number) => number;
  getY: (y: number) => number;
  region: RegionType;
  colourOffset: number;
}

function Region({
  getX,
  getY,
  region: {land, children},
  colourOffset,
}: RegionProps) {
  const {landBeingEdited, landIsHovered, editingMode} = useEditingLands();
  const left = `${getX(land.x1)}%`;
  const top = `${getY(land.y1)}%`;
  const width = `${getX(land.x2) - getX(land.x1)}%`;
  const height = `${getY(land.y2) - getY(land.y1)}%`;
  return (
    <>
      <div
        className={classnames('continent__region', {
          'continent__region--edited': landBeingEdited(land),
          'continent__region--hovered': !editingMode && landIsHovered(land),
        })}
        style={{
          left,
          top,
          width,
          height,
          backgroundColor: greens[colourOffset % greens.length],
        }}
        title={`(${land.x1}, ${land.y1}) -> (${land.x2}, ${land.y2})`}
      />
      <Regions
        getX={getX}
        getY={getY}
        regions={children}
        colourOffset={colourOffset + 1}
      />
    </>
  );
}

interface Props {
  getX: (x: number) => number;
  getY: (y: number) => number;
  regions: RegionType[];
  colourOffset: number;
}

export default function Regions({getX, getY, regions, colourOffset}: Props) {
  return (
    <>
      {regions.map((region, i) => (
        <Region
          key={`region${colourOffset}-${i}`}
          region={region}
          getX={getX}
          getY={getY}
          colourOffset={colourOffset}
        />
      ))}
    </>
  );
}
