import React from 'react';
import classnames from 'classnames';

import './OverlappingLand.scss';
import {Land} from './Land';
import {useEditingLands} from './LandContext';

interface Props {
  getX: (x: number) => number;
  getY: (y: number) => number;
  land: Land;
}

export default function OverlappingLand({getX, getY, land}: Props) {
  const {landBeingEdited, landIsHovered, editingMode} = useEditingLands();
  const left = `${getX(land.x1)}%`;
  const top = `${getY(land.y1)}%`;
  const width = `${getX(land.x2) - getX(land.x1)}%`;
  const height = `${getY(land.y2) - getY(land.y1)}%`;
  return (
    <div
      className={classnames('overlapping-land', {
        'overlapping-land--hovered': !editingMode && landIsHovered(land),
        'overlapping-land--edited': landBeingEdited(land),
      })}
      title={`(${land.x1}, ${land.y1}) -> (${land.x2}, ${land.y2})`}
      style={{
        left,
        top,
        width,
        height,
      }}
    />
  );
}
