import React, {useState, useEffect} from 'react';

import {
  useCurrentLands,
  useUpdateCurrentLands,
  useCurrentRegions,
  useEditLands,
  useEditingLands,
} from './LandContext';
import {Land, sameLandAs} from './Land';
import LandForm from './LandForm';

export default function LandForms() {
  const lands = useCurrentLands();
  const {overlappingLands} = useCurrentRegions();
  const {updateLand, deleteLand, newLand} = useUpdateCurrentLands();
  const {setHoveredLand} = useEditLands();
  const {editingMode} = useEditingLands();
  const [showNew, setShowNew] = useState(false);
  useEffect(() => {
    setShowNew(false);
  }, [lands]);
  return (
    <>
      <ul className="land-forms">
        {lands.map((land, index) => (
          <li
            key={`landForm${index}${JSON.stringify(land)}`}
            className="land-forms__form"
            onMouseOver={() => setHoveredLand(land)}
            onMouseLeave={() => setHoveredLand(undefined)}>
            <LandForm
              land={land}
              submit={(land: Land) => updateLand(index, land)}
              deleteLand={() => {
                deleteLand(index);
              }}
              overlapping={overlappingLands.some(sameLandAs(land))}
            />
          </li>
        ))}
        {showNew && (
          <li className="land-forms__form">
            <LandForm
              land={undefined}
              submit={(land: Land) => newLand(land)}
              deleteLand={undefined}
              overlapping={false}
            />
          </li>
        )}
      </ul>
      {!showNew && !editingMode && (
        <button
          title="Add a new land mass"
          type="button"
          onClick={() => {
            setShowNew(true);
          }}>
          <span role="img" aria-label="Add new">
            ‚ûï
          </span>
        </button>
      )}
    </>
  );
}
