import React, {useState, useCallback} from 'react';
import classnames from 'classnames';

import './LandForm.scss';
import './Modal.scss';
import {Land} from './Land';
import {useEditLands, useEditingLands} from './LandContext';

interface InputProps {
  value: string;
  set: (newValue: string) => void;
  editing: boolean;
  error: boolean;
}

function Input({value, set, editing, error}: InputProps) {
  const onChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      set(event.target.value);
    },
    [set],
  );
  if (!editing) {
    return (
      <span className="land-form__input land-form__input--readonly">
        {value}
      </span>
    );
  }
  return (
    <input
      className={classnames('land-form__input', {
        'land-form__input--error': error,
      })}
      value={value !== undefined ? value.toString() : ''}
      onChange={onChange}
    />
  );
}

interface Props {
  land: Land | undefined;
  submit: (updated: Land) => void;
  deleteLand: (() => void) | undefined;
  overlapping: boolean;
}

export default function LandForm({
  land,
  submit,
  deleteLand,
  overlapping,
}: Props) {
  const {setEditingLand} = useEditLands();
  const {landBeingEdited, landIsHovered, editingMode} = useEditingLands();
  const editing = land ? landBeingEdited(land) : !editingMode;
  const [errors, setErrors] = useState<string[]>([]);
  const [submitted, setSubmitted] = useState(false);
  const [showSave, setShowSave] = useState(false);
  const [x1string, setX1] = useState(land ? land.x1.toString() : '');
  const [x2string, setX2] = useState(land ? land.x2.toString() : '');
  const [y1string, setY1] = useState(land ? land.y1.toString() : '');
  const [y2string, setY2] = useState(land ? land.y2.toString() : '');
  const validate = useCallback(() => {
    const newErrors = [];
    const x1 = Number.parseFloat(x1string);
    const x2 = Number.parseFloat(x2string);
    const y1 = Number.parseFloat(y1string);
    const y2 = Number.parseFloat(y2string);
    if (Number.isNaN(x1)) {
      newErrors.push('x1');
    }
    if (Number.isNaN(x2)) {
      newErrors.push('x2');
    }
    if (Number.isNaN(y1)) {
      newErrors.push('y1');
    }
    if (Number.isNaN(y2)) {
      newErrors.push('y2');
    }
    if (newErrors.length === 0) {
      if (x1 >= x2) {
        newErrors.push('x1');
        newErrors.push('x2');
      }
      if (y1 >= y2) {
        newErrors.push('y1');
        newErrors.push('y2');
      }
    }
    setErrors(newErrors);
    setSubmitted(true);
    return newErrors.length === 0 ? {x1, x2, y1, y2} : undefined;
  }, [x1string, x2string, y1string, y2string]);
  const trySubmit = useCallback(() => {
    setSubmitted(true);
    const newLand = validate();
    if (newLand) {
      submit(newLand);
      setEditingLand(undefined);
    }
  }, [setEditingLand, submit, validate]);
  const withValidate = useCallback(
    (setter: (value: string) => void) => (value: string) => {
      setter(value);
      if (submitted) {
        validate();
      }
    },
    [submitted, validate],
  );
  return (
    <div
      className={classnames('land-form', {
        'land-form--overlapping': overlapping && !editingMode,
        'land-form--edited': editing,
        'land-form--hovered':
          land && !editingMode ? landIsHovered(land) : false,
      })}>
      <div className="land-form__inputs">
        (
        <Input
          editing={editing}
          value={x1string}
          set={withValidate(setX1)}
          error={errors.includes('x1')}
        />
        ,{' '}
        <Input
          editing={editing}
          value={y1string}
          set={withValidate(setY1)}
          error={errors.includes('y1')}
        />
        ) ‚Üí (
        <Input
          editing={editing}
          value={x2string}
          set={withValidate(setX2)}
          error={errors.includes('x2')}
        />
        ,{' '}
        <Input
          editing={editing}
          value={y2string}
          set={withValidate(setY2)}
          error={errors.includes('y2')}
        />
        )
      </div>
      <div className="land-form__actions">
        {editing && deleteLand && (
          <button
            className="land-form__action"
            type="button"
            title="Cancel"
            onClick={() => setEditingLand(undefined)}>
            <span role="img" aria-label="Cancel">
              üîô
            </span>
          </button>
        )}
        {editing && (
          <button
            className="land-form__action"
            type="button"
            title="Save"
            onClick={trySubmit}>
            <span role="img" aria-label="Save">
              üíæ
            </span>
          </button>
        )}
        {!editing && !editingMode && (
          <button
            className="land-form__action"
            type="button"
            title="Edit"
            onClick={() => setEditingLand(land)}>
            <span role="img" aria-label="Edit">
              ‚úèÔ∏è
            </span>
          </button>
        )}
        {!editing && !editingMode && deleteLand && (
          <>
            <button
              className="land-form__action"
              type="button"
              title="Delete"
              onClick={() => setShowSave(true)}>
              <span role="img" aria-label="Delete">
                ‚ùå
              </span>
            </button>
            <div className={showSave ? 'modal modal--visible' : 'modal'}>
              <div className="modal__wrapper">
                <p className="modal__heading">Delete this land mass?</p>
                <div className="modal__buttons">
                  <button
                    type="button"
                    title="Confirm deletion"
                    className="modal__button modal__button--ok"
                    onClick={() => {
                      setShowSave(false);
                      deleteLand();
                    }}>
                    OK
                  </button>
                  <button
                    type="button"
                    title="Close"
                    className="modal__button modal__button--close"
                    onClick={() => setShowSave(false)}>
                    Cancel
                  </button>
                </div>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
